import React, { useContext, useState, useEffect, useRef } from 'react';
import './SubSectorScroll.css';
import { AppContext } from '../context/app.context';
import Capsule from './Capsule2';

const SubSectorScroll = ({ listItems, onClick, selected }) => {
  const session = useContext(AppContext);
  const [items, setItems] = useState([]);
  const scrollContainerRef = useRef(null);

  useEffect(() => {
    // Initialize items with the provided listItems
    const initialItems = listItems?.map((content, i) => ({
      ...content, // Spread content properties if they are objects
      isActive: i === 0 // Only the first item is active initially
    }));
    setItems(initialItems);
  }, [listItems]);

  useEffect(() => {
    const handleScroll = () => {
      const scrollContainer = scrollContainerRef.current;
      const itemHeight = scrollContainer.querySelector('li')?.offsetHeight;
      if (!itemHeight) return; // Guard against no items

      const centerPosition = scrollContainer.scrollTop + scrollContainer.clientHeight / 2;
      const centerIndex = Math.round(centerPosition / itemHeight - 0.5);

      setItems(currentItems =>
        currentItems.map((item, index) => ({
          ...item,
          isActive: index === centerIndex,
        }))
      );
    };

    const currentScrollContainer = scrollContainerRef.current;
    currentScrollContainer.addEventListener('scroll', handleScroll);

    return () => {
      currentScrollContainer.removeEventListener('scroll', handleScroll);
    };
  }, []); // Empty dependency array ensures this effect runs once

  return (
    <div className='curved-div' id="scroll-container-sub" style={{ background: session?.themeSettings?.secondary_background_color }}>
      <div className="wrap-container" ref={scrollContainerRef}>
        <ul id="ul-scroll-sub">
          {items.map((item, index) => (
            <li key={index} className={item.isActive ? 'active' : ''}>
              {/* {console.log(item)} */}
              <Capsule
                key={item.id}
                item={item}
                selected={selected}
                onClick={onClick}
                index={index}
                items={items}
              />
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default SubSectorScroll;
