import React, { useRef, useEffect, useState, useContext } from 'react';
import { useSpring, animated, to } from '@react-spring/web';
import { useGesture } from 'react-use-gesture';
import imgs from '../data';
import { AppContext } from '../context/app.context';
import Image from "./map.svg"
import MapComponent from "../MapComponent"
import TractorAnimation from "./tractor.gif"
import CraneAnimation from "./crane.gif"
import Cloud from "./cloud.png"

const dataToBeSend = { imageURL: imgs[0] }

const calcX = (y, ly) => -(y - ly - window.innerHeight / 2) / 20;

const calcY = (x, lx) => (x - lx - window.innerWidth / 2) / 20;

export default function App({ socket, resizable, showOnOtherScreens }) {
    const [isShownOnScreen, setIsShownOnScreen] = useState(undefined)
    const isEventTriggered = useRef(false)
    const session = useContext(AppContext)

    // useEffect(() => {
    //     const preventDefault = (e) => e.preventDefault();
    //     if (document.getElementById("menu-toggle")) {
    //         document.getElementById("menu-toggle").addEventListener('gesturestart', preventDefault);
    //         document.getElementById("menu-toggle").addEventListener('gesturechange', preventDefault);

    //         return () => {
    //             document.getElementById("menu-toggle").removeEventListener('gesturestart', preventDefault);
    //             document.getElementById("menu-toggle").removeEventListener('gesturechange', preventDefault);
    //         };
    //     }
    // }, []);

    const domTarget = useRef(null);

    const [{ x, y, rotateX, rotateY, rotateZ, zoom, scale }, api] = useSpring(() => ({
        rotateX: 0,
        rotateY: 0,
        rotateZ: 0,
        scale: 1,
        zoom: 0,
        x: 100,
        y: 100,
        config: { mass: 5, tension: 350, friction: 40 },
    }));

    const [wheelApi] = useSpring(() => ({ wheelY: 0 }));

    const movingThreshold = 0.1
    useGesture(
        {
            onDrag: ({ active, offset: [x, y], direction: [dirX, dirY], velocity }) => {
                if (session.stopDrag.current) {
                    return
                }
                const { height, width } = document.getElementById('component1').getBoundingClientRect();
                // if (x < 0) {
                //     x = 0;
                // }
                // if (x > window.innerWidth - width) {
                //     x = window.innerWidth - width;
                // }
                // if (y < 0) {
                //     y = 0;
                // }
                // if (y > window.innerHeight - height) {
                //     y = window.innerHeight - height;
                // }

                if (active && velocity > 4 && !isEventTriggered.current && showOnOtherScreens) {
                    const isHorizontalMovement = Math.abs(dirX) > Math.abs(dirY);
                    if (isHorizontalMovement) {
                        if (dirX > 0) {
                            console.log("right");
                            socket.current.emit("move", { destination: "DISPLAY_3", data: dataToBeSend, component: ["COMPONENT1"] });
                            setIsShownOnScreen("DISPLAY_3");
                            isEventTriggered.current = true
                        } else {
                            console.log("left");
                            socket.current.emit("move", { destination: "DISPLAY_1", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_1");
                            isEventTriggered.current = true
                        }
                    } else if (Math.abs(dirY) > movingThreshold) {
                        if (dirY < 0) {
                            console.log("up");
                            socket.current.emit("move", { destination: "DISPLAY_2", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_2");
                            isEventTriggered.current = true
                        }
                    }
                }
                api({ x, y, rotateX: 0, rotateY: 0, scale: active ? 1 : 1.1 });
            },

            onPinch: ({ offset: [d, a] }) => api({ zoom: d / 200, rotateZ: a }),
            onMove: ({ xy: [px, py], dragging }) =>
                !dragging &&
                api({
                    rotateX: calcX(py, y.get()),
                    rotateY: calcY(px, x.get()),
                    scale: 1.1,
                })
            ,
            onHover: ({ hovering }) =>
                !hovering && api({ rotateX: 0, rotateY: 0, scale: 1 }),
            onWheel: ({ event, offset: [, y] }) => {
                event.preventDefault();
                // wheelApi.set({ wheelY: y });
            },
        },
        { domTarget, eventOptions: { passive: false } }
    );
    const [toggled, setToggledTo] = useState(false);
    // const { toggled, setToggledTo } = session
    const [clicked, setClicked] = useState(true)
    const [stateClicked, setStateClicked] = useState(undefined)
    const [sectorClicked, setSectorClicked] = useState(undefined)
    const [districtSelected, setDistrictSelected] = useState(undefined)
    const [subSectorSelected, setSubSectorSelected] = useState(undefined)

    const [stateArr, setStateArr] = useState([]);
    const [sectorArr, setSectorArr] = useState([]);
    const [districtArr, setDistrictArr] = useState([]);
    const [subSectorArr, setSubSectorArr] = useState([]);


    const handleOnClick = () => {
        setDistrictSelected(undefined);
        setSubSectorSelected(undefined);
        setToggledTo(!toggled);
        setSectorClicked(undefined);
        setClicked(true);
        setStateClicked(undefined)
        // console.log("Component 2")
    };

    return (
        <div className='cloud' style={{
            backgroundImage: `url(${Cloud})`,
        }}>
            <img src={TractorAnimation} id="movingImage" style={{
                width: '300px', height: '300px', position: 'fixed',
                top: '850px'
            }} />
            {/* <img src={Cloud} style={{
                top: '50px'
            }} /> */}
            <img src={CraneAnimation} style={{
                width: '500px', height: '500px', position: 'fixed',
                top: '650px',
                right: 0,
                transform: 'rotate(0deg)'
            }} />
            <div className="bird-container bird-container-one">
                <div className="bird bird-one"></div>
            </div>
            {/* <div className="bird-container bird-container-two">
                <div className="bird bird-two"></div>
            </div> */}
            <div className="bird-container bird-container-three">
                <div className="bird bird-three"></div>
            </div>
            <div className="bird-container bird-container-four">
                <div className="bird bird-four"></div>
            </div>
            <animated.div
                id="component1"
                className="card"
                style={{
                    transform: 'perspective(600px)',
                    x,
                    y,
                    scale: resizable ? to([scale, zoom], (s, z) => s + z) : undefined,

                    // rotateX,
                    // rotateY,
                    // rotateZ,
                }}
            >

                <div id="app-container">
                    <div id="app">
                        {session.masterData?.state?.length > 0 &&
                            <MapComponent
                                clicked={clicked}
                                setClicked={setClicked}
                                districtSelected={districtSelected}
                                setDistrictSelected={setDistrictSelected}
                                setSubSectorSelected={setSubSectorSelected}
                                subSectorSelected={subSectorSelected}
                                sectorClicked={sectorClicked}
                                setSectorClicked={setSectorClicked}
                                stateClicked={stateClicked}
                                setStateClicked={setStateClicked}
                                toggled={toggled}
                                setToggledTo={setToggledTo}
                            />
                        }
                        <div ref={domTarget} id="menu-toggle" onClick={handleOnClick}>
                            <img src={session?.themeSettings?.explore_image} style={{ width: '400px', height: '400px' }} />
                        </div>

                    </div>
                </div>
                {isShownOnScreen &&
                    <button onClick={async () => {
                        socket.current.emit("close", { destination: isShownOnScreen, component: 'COMPONENT1' });
                        setIsShownOnScreen(undefined);
                        isEventTriggered.current = false
                    }}>Close</button>
                }

            </animated.div>
        </div>
    );
}
