import React, { useContext } from 'react';
import { AppContext } from '../context/app.context';

// Helper function to capitalize the first letter of each word
const capitalizeFirstLetterOfEachWord = (str) => {
  return str.replace(/\b\w/g, char => char?.toUpperCase());
}

const Capsule2 = ({ item, selected, onClick, index, items, x, y }) => {
  const session = useContext(AppContext)
  return (
    <div
      key={index}
      onClick={() => onClick ? onClick(item) : {}}
      className={selected === item?.id ? "row selected-cap2" : "row "}
      style={{
        transform: `scaleX(-1)`
      }}
    >
      {/* <div className='logo-div col-3'>
        <img className='logo' src={item.image_url} alt={item.name} />
        </div> */}
      <div className='col' style={{
        transform: `translate(${x}px, ${y}px)`
      }}>
        <p className="cap-p" style={{ color: selected === item.id ? session?.themeSettings?.selected_text_color : session?.themeSettings?.unselected_text_color }}>
          {item.name}
          {/* {capitalizeFirstLetterOfEachWord(item.name.toLowerCase())} */}
        </p>
      </div>

    </div>
  );
};

export default Capsule2;
