import React, { createContext, useEffect, useRef, useState, } from 'react';
import constants from '../constants';
import themes from "../data/theme"

export const AppContext = createContext();

const AppContextProvider = ({ children, socket }) => {
    const [masterData, setMasterData] = useState();
    const [toggled, setToggledTo] = useState(false);
    const [selectedTheme, setSelectedTheme] = useState('default')
    const [themeSettings, setThemeSettings] = useState(themes[selectedTheme])
    const stopDrag = useRef(false)

    useEffect(() => {
        let settings = themes[selectedTheme] || themes["default"]
        setThemeSettings(settings)
        document.body.style.backgroundImage = `url(${settings.main_background_image})`
    }, [selectedTheme])

    const decodeUint8ArrayToString = (uint8Array) => {
        const decoder = new TextDecoder('utf-8');
        return decoder.decode(uint8Array);
    }

    const getData = (obj) => {
        console.log("called", masterData, socket)
        if (masterData && masterData[obj?.type?.toLowerCase()]) {
            let type = obj.type?.toLowerCase()
            const data = masterData[type]
            if (obj.label && obj.value) {
                const finalData = data.filter((o) => o[obj.label] === obj.value)
                return finalData
            }
            return data
        } else {
            if (socket) {
                socket.emit(constants.GET_MASTER_DATA, { type: constants.MASTER })
                socket.on(constants.GET_MASTER_DATA, (data) => {
                    if (data) {
                        const uint8Array = new Uint8Array(data);
                        const jsonString = decodeUint8ArrayToString(uint8Array);
                        setMasterData(JSON.parse(jsonString))
                    }
                });
            }
        }
    }

    useEffect(() => {
        getData("STATE")
    }, [socket])

    const refreshData = () => {
        if (socket && socket) {
            socket.emit(constants.GET_MASTER_DATA, { type: constants.REFRESH })
        }
    }

    return (
        <AppContext.Provider
            value={{
                masterData,
                setMasterData,
                getData,
                refreshData,
                toggled,
                setToggledTo,
                stopDrag,
                selectedTheme,
                setSelectedTheme,
                themeSettings
            }}>
            {children}
        </AppContext.Provider>
    );
};

export default AppContextProvider;
