import React, { useContext, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faXmark } from '@fortawesome/free-solid-svg-icons';
import './persona.css'; // Make sure to create a CSS file for the styles.
import { AppContext } from '../context/app.context';

const CollageCircle = () => {
  const session = useContext(AppContext)
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <nav>
      <span id="menu-icon" onClick={toggleMenu} style={{ background: session?.themeSettings?.secondary_background_color }}>
        <FontAwesomeIcon icon={isOpen ? faXmark : faBars} />
      </span>
      <a className={`menu-item one ${isOpen ? 'active' : ''}`} href="#">
        <div className='persona' style={{
          backgroundImage: `url('./asset/persona/annadata.jpg')`
        }}>
          <p className='persona-text'>annadata</p>
        </div>
      </a>
      <a className={`menu-item two ${isOpen ? 'active' : ''}`} href="#">
        <div className='persona' style={{
          backgroundImage: `url('./asset/persona/industry.jpg')`
        }}>
          <p className='persona-text'>industry</p>
        </div>
      </a>
      <a className={`menu-item three ${isOpen ? 'active' : ''}`} href="#">
        <div className='persona' style={{
          backgroundImage: `url('./asset/persona/nari.jpg')`
        }}>
          <p className='persona-text'>nari</p>
        </div>
      </a>
      <a className={`menu-item four ${isOpen ? 'active' : ''}`} href="#">
        <div className='persona' style={{
          backgroundImage: `url('./asset/persona/poor.jpg')`
        }}>
          <p className='persona-text'>gareeb</p>
        </div>
      </a>
      <a className={`menu-item five ${isOpen ? 'active' : ''}`} href="#">
        <div className='persona' style={{
          backgroundImage: `url('./asset/persona/youth.jpg')`
        }}>
          <p className='persona-text'>youth</p>
        </div>
      </a>
    </nav>
  );
};

export default CollageCircle;
