import React, { useRef, useEffect, useState, useContext } from 'react';
import { useSpring, animated, to } from '@react-spring/web';
import { useGesture } from 'react-use-gesture';
import imgs from '../data';
import { AppContext } from '../context/app.context';
import Image from "./indicators.png"
import Indicators from '../Indicators';
import data from "../data/indicator.json";

const dataToBeSend = { imageURL: imgs[0] }

const calcX = (y, ly) => -(y - ly - window.innerHeight / 2) / 20;

const calcY = (x, lx) => (x - lx - window.innerWidth / 2) / 20;

export default function App({ socket, resizable, showOnOtherScreens }) {
    const [isShownOnScreen, setIsShownOnScreen] = useState(undefined)
    const isEventTriggered = useRef(false)
    const session = useContext(AppContext)


    const domTarget = useRef(null);

    const [{ x, y, rotateX, rotateY, rotateZ, zoom, scale }, api] = useSpring(() => ({
        rotateX: 0,
        rotateY: 0,
        rotateZ: 0,
        scale: 1,
        zoom: 0,
        x: 0,
        y: 0,
        config: { mass: 5, tension: 350, friction: 40 },
    }));

    const [wheelApi] = useSpring(() => ({ wheelY: 0 }));

    const movingThreshold = 0.1
    useGesture(
        {
            onDrag: ({ active, offset: [x, y], direction: [dirX, dirY], velocity }) => {
                if (session.stopDrag.current) {
                    return
                }

                if (active && velocity > 4 && !isEventTriggered.current && showOnOtherScreens) {
                    const isHorizontalMovement = Math.abs(dirX) > Math.abs(dirY);
                    if (isHorizontalMovement) {
                        if (dirX > 0) {
                            console.log("right");
                            socket.current.emit("move", { destination: "DISPLAY_3", data: dataToBeSend, component: ["COMPONENT1"] });
                            setIsShownOnScreen("DISPLAY_3");
                            isEventTriggered.current = true
                        } else {
                            console.log("left");
                            socket.current.emit("move", { destination: "DISPLAY_1", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_1");
                            isEventTriggered.current = true
                        }
                    } else if (Math.abs(dirY) > movingThreshold) {
                        if (dirY < 0) {
                            console.log("up");
                            socket.current.emit("move", { destination: "DISPLAY_2", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_2");
                            isEventTriggered.current = true
                        }
                    }
                }
                api({ x, y, rotateX: 0, rotateY: 0, scale: active ? 1 : 1.1 });
            },

            onPinch: ({ offset: [d, a] }) => api({ zoom: d / 200, rotateZ: a }),
            onMove: ({ xy: [px, py], dragging }) =>
                !dragging &&
                api({
                    rotateX: calcX(py, y.get()),
                    rotateY: calcY(px, x.get()),
                    scale: 1.1,
                })
            ,
            onHover: ({ hovering }) =>
                !hovering && api({ rotateX: 0, rotateY: 0, scale: 1 }),
            onWheel: ({ event, offset: [, y] }) => {
                event.preventDefault();
                // wheelApi.set({ wheelY: y });
            },
        },
        { domTarget, eventOptions: { passive: false } }
    );
    const [toggled, setToggledTo] = useState(false);
    // const { toggled, setToggledTo } = session
    const [clicked, setClicked] = useState(true)
    const [stateClicked, setStateClicked] = useState(undefined)
    const [sectorClicked, setSectorClicked] = useState(undefined)
    const [districtSelected, setDistrictSelected] = useState(undefined)
    const [subSectorSelected, setSubSectorSelected] = useState(undefined)
    const [selectedIndicator, setSelectedIndicator] = useState([]);

    const handleOnClick = () => {
        setDistrictSelected(undefined);
        setSubSectorSelected(undefined);
        setToggledTo(!toggled);
        setSectorClicked(undefined);
        setClicked(true);
        setStateClicked(undefined)
        // console.log("Component 2")
    };


    return (
        <>
            <animated.div
                id="component4"
                className="card"
                style={{
                    transform: 'perspective(600px)',
                    x,
                    y,
                    scale: resizable ? to([scale, zoom], (s, z) => s + z) : undefined,
                }}
            >
                <div id="app-container">
                    <div id="app">
                        <div ref={domTarget} id="menu-toggle-indicator" onClick={handleOnClick}>
                            <img src={session?.themeSettings.adp_image} onClick={handleOnClick} />
                        </div>
                    </div>
                </div>
                {isShownOnScreen &&
                    <button onClick={async () => {
                        socket.current.emit("close", { destination: isShownOnScreen, component: 'COMPONENT1' });
                        setIsShownOnScreen(undefined);
                        isEventTriggered.current = false
                    }}>Close</button>
                }

            </animated.div>

        </>
    );
}
