
import React, { useContext, useState, useEffect, useRef } from "react";
import "./StateScroll.css";
import { AppContext } from "../context/app.context";
// Assuming Capsule is your component for rendering each item
import Capsule from "./Capsule";

const StateScroll = ({ listItems, onClick, selected }) => {
  const session = useContext(AppContext);
  const [items, setItems] = useState([]);
  const scrollContainerRef = useRef(null);

  useEffect(() => {
    // Initialize items with the provided listItems
    const initialItems = listItems?.map((content, i) => ({
      ...content,
      isActive: i === 0 // Only the first item is active initially
    }));
    setItems(initialItems);
  }, [listItems]);

  // Define a new click handler that updates selection based on current state
  const handleItemClick = (item) => {
    const isSelected = selected.includes(item.id);
    if (isSelected) {
      // Remove from selection
      onClick(selected.filter(id => id !== item.id));
    } else {
      // Add to selection
      onClick([...selected, item.id]);
    }
  };

  useEffect(() => {
    const handleScroll = () => {
      const scrollContainer = scrollContainerRef.current;
      if (!scrollContainer) return;

      const visibleItems = [];
      const children = Array.from(scrollContainer.querySelectorAll('li'));

      // Calculate visible bounds
      const viewportTop = scrollContainer.scrollTop;
      const viewportBottom = viewportTop + scrollContainer.clientHeight;

      children.forEach((child, index) => {
        const childTop = child.offsetTop;
        const childBottom = childTop + child.offsetHeight;

        // Check if the item is within the visible viewport
        if (childBottom > viewportTop && childTop < viewportBottom) {
          visibleItems.push({ index, element: child });
        }
      });

      // Find the middle visible item
      if (visibleItems.length) {
        const middleIndex = Math.floor(visibleItems.length / 2);
        const middleItem = visibleItems[middleIndex];

        setItems(currentItems =>
          currentItems.map((item, index) => ({
            ...item,
            isActive: index === middleItem.index,
          }))
        );
      }
    };

    const currentScrollContainer = scrollContainerRef.current;
    currentScrollContainer.addEventListener('scroll', handleScroll);

    return () => {
      currentScrollContainer.removeEventListener('scroll', handleScroll);
    };
  }, []); // Empty dependency array ensures this effect runs once

  return (
    <div id="scroll-container-state">
      <div className="wrap-container" ref={scrollContainerRef}>
        <ul id="ul-scroll">
          {items.map((item, index) => (
            <li key={index} className={item?.isActive ? "active" : ""}>
              <Capsule
                key={item.id}
                item={item}
                selected={selected}
                onClick={onClick}
                index={index}
                items={items}
              />
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default StateScroll;


