import React, { useContext } from 'react';
import "./SectorScroll.css"
import { AppContext } from '../context/app.context';

// Helper function to capitalize the first letter of each word
const capitalizeFirstLetterOfEachWord = (str) => {
  return str.replace(/\b\w/g, char => char.toUpperCase());
}

const Capsule = ({ item, selected, onClick, index, items, x, y }) => {
  const session = useContext(AppContext)
  return (
    <div
      key={index}
      onClick={() => onClick ? onClick(item) : {}}
      className={selected === item.id ? "row radial-list-item  selected-capsule" : "row radial-list-item capsule"}
      style={{
        transform: `translate(${x}px, ${y}px)`,
        display: "flex",
        alignItems: "center",
        width: "350px",
        justifyContent: "space-between",
        paddingRight: "30px",
        // marginLeft: item.isActive ? "100px" : "0px",
        background: selected === item.id ? session?.themeSettings?.selected_button_background_color : session?.themeSettings?.unselected_button_background_color
      }}
    >
      <div className='logo-div col'>
        <img className='logo' src={item.image_url} alt={item.name} />
      </div>
      <div className='col'>
        <p className="radial-p" style={{ color: selected === item.id ? session?.themeSettings?.selected_text_color : session?.themeSettings?.unselected_text_color }}>
          {item.name}
          {/* {capitalizeFirstLetterOfEachWord(item.name.toLowerCase())} */}
        </p>
      </div>

    </div >
  );
};

export default Capsule;
