import React, { useRef, useEffect, useState } from 'react';
import { io } from "socket.io-client";
import Component2 from "./Component2"
import Component3 from "./Component3"
import Component4 from "./Component4"
import Component5 from "./Component5"
import Personas from "./Personas"
import Constants from "./constants"
import AppContextProvider from './context/app.context';
import { useScroll } from 'react-use-gesture';

const App = () => {
  const socket = useRef();
  const [socketState, setSocketState] = useState(socket.current);

  useEffect(() => {
    socket.current = io(Constants.SOCKET_CONNECTION_URL);
    socket.current.on(Constants.CONNECTION, () => {
      console.log("connected to server");
      setSocketState(socket.current);
    });

    socket.current.on(Constants.DISCONNECTION, () => {
      console.log("Disconnected");
      setSocketState(socket.current);
    });

    return () => {
      if (socket.current && socket.current.connected) {
        socket.current.disconnect();
      }
    };
  }, []);

  return (
    <div className="container">
      <AppContextProvider socket={socketState}>
        <Component2 socket={socket} ></Component2>
        <Component4 socket={socket} ></Component4>
        <Component5 socket={socket} ></Component5>
        <Personas socket={socket}></Personas>
      </AppContextProvider>
    </div>
  );
}


export default function AppWrapper() {
  return (
    <App />
  )
}