import React, { useEffect, useContext, useState } from "react";
import "./index.css";
import { AppContext } from "../context/app.context";
import SubSector from "../SubSector";
import StateRadial from "../State";
import DistrictScroll from "../District/DistrictScroll";
import SectorScroll from "../Sector/SectorScroll";

const classNames = (classes) =>
  Object.entries(classes)
    .filter(([key, value]) => value)
    .map(([key]) => key)
    .join(" ");




const MenuOption = (props) => {
  const { toggled } = useContext(AppContext);

  const className = `menu-${props.type}-option`;
  const delay = toggled ? 200 : 0;

  const styles = {
    transitionDelay: `${delay + 50 * props.index}ms`,
  };

  return (
    <div
      className={className}
      disabled={!toggled}
      style={{
        ...styles,
        backgroundColor: props.selected
          ? " rgba(181, 198, 69, 0.20)"
          : props.onClick
            ? "transparent"
            : "transparent",
        color: "white",
        borderRadius: "67.636px",
        border: props.selected
          ? "2px solid #87CC2E"
          : props.onClick
            ? "2px solid #87CC2E"
            : "none",
        boxShadow: props.selected
          ? "0px 4px 67.9px 0px #99B000 inset"
          : props.onClick
            ? "none"
            : "none",
      }}
      onClick={props.onClick}
    >
      {props.iconPosition === "right" && props.type == "quick" ? (
        <div className="logo_div">
          {" "}
          <img className="logo_sector" src={props.imgSrc} alt="logo" />{" "}
        </div>
      ) : null}
      <h3 className="label"> {props.label}</h3>
      {props.iconPosition === "left" && props.type == "quick" ? (
        <div className="logo_div">
          {" "}
          <img className="logo_sector" src={props.imgSrc} alt="logo" />{" "}
        </div>
      ) : null}
    </div>
  );
};

const Menu = ({
  clicked,
  setClicked,
  sectorClicked,
  setSectorClicked,
  stateClicked,
  setStateClicked,
  districtSelected,
  setDistrictSelected,
  setSubSectorSelected,
  subSectorSelected,
  toggled,
  setToggledTo,
}) => {
  // const { toggled, getData, setToggledTo } = useContext(AppContext);
  const { getData, setSelectedTheme } = useContext(AppContext);
  const getQuickOptions = () => {
    let data = getData({
      type: "SECTOR",
    });
    return data;
  };

  const getStateOptions = () => {
    let data = getData({
      type: "STATE",
    });
    data = data.map((o) => {
      return {
        ...o,
        name: o.state,
        id: o.state_lgd_code,
      };
    });
    data.sort((a, b) => {
      const nameA = a.name.toUpperCase(); // ignore upper and lowercase
      const nameB = b.name.toUpperCase(); // ignore upper and lowercase
      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    });
    return data;
  };

  const getFullOptions = () => {
    let data = getData({
      type: "SUB_SECTOR",
      label: "sector_id",
      value: sectorClicked,
    });

    // Create a new array with one blank elements at the beginning and end
    //   data = [{name: ''}, ...data,  {name: ''}];
    return data;
  };

  const getDistrictOptions = () => {
    let data = getData({
      type: "DISTRICT",
      label: "state_lgd_code",
      value: stateClicked,
    });
    data = data.map((o) => {
      return {
        ...o,
        name: o.district,
        id: o.district_lgd_code,
      };
    });
    return data;
  };

  return (
    <div id="menu" className={classNames({ toggled })}>
      {clicked && (
        <div id="menu-background-wrapper">
          <div id="menu-background" />
        </div>
      )}

      {/* sector scroll */}
      {clicked && (
        <SectorScroll
          listItems={getQuickOptions()}
          selected={sectorClicked}
          onClick={(obj) => { setSectorClicked(obj.id); setSelectedTheme(obj.name?.toLowerCase()) }}
        />
      )}

      {/* state scroll */}
      {clicked && (
        <StateRadial
          listItems={getStateOptions()}
          selected={stateClicked}
          onClick={(obj) => setStateClicked(obj.id)}
        />
      )}

      {/* sub sector scroll */}
      {sectorClicked && (
        <SubSector
          listItems={getFullOptions()}
          selected={subSectorSelected}
          onClick={(obj) => setSubSectorSelected(obj.id)}
        />
      )}

      {/* district scroll */}
      {stateClicked && (
        <DistrictScroll
          listItems={getDistrictOptions()}
          selected={districtSelected}
          onClick={(obj) => setDistrictSelected(obj.id)}
        />
      )}
    </div>
  );
};

export default Menu;
