import React, { useRef, useEffect, useState, useContext } from 'react';
import { useSpring, animated, to } from '@react-spring/web';
import { useGesture } from 'react-use-gesture';
import imgs from '../data';
import { AppContext } from '../context/app.context';
import CollageCircle from './CollageCircle';

const dataToBeSend = { imageURL: imgs[0] }

const calcX = (y, ly) => -(y - ly - window.innerHeight / 2) / 20;

const calcY = (x, lx) => (x - lx - window.innerWidth / 2) / 20;

export default function App({ socket, resizable, showOnOtherScreens }) {
    const [isShownOnScreen, setIsShownOnScreen] = useState(undefined)
    const isEventTriggered = useRef(false)
    const session = useContext(AppContext)


    const domTarget = useRef(null);

    const [{ x, y, }, api] = useSpring(() => ({
        x: 500, // Initial x value
        y: 500, // Initial y value
        config: { mass: 5, tension: 350, friction: 40 },
    }));

    const [wheelApi] = useSpring(() => ({ wheelY: 0 }));

    const movingThreshold = 0.1
    useGesture(
        {
            onDrag: ({ movement: [mx, my], direction: [dirX, dirY], velocity }) => {
                if (session.stopDrag.current) {
                    return
                }

                if (velocity > 4 && !isEventTriggered.current && showOnOtherScreens) {
                    const isHorizontalMovement = Math.abs(dirX) > Math.abs(dirY);
                    if (isHorizontalMovement) {
                        if (dirX > 0) {
                            console.log("right");
                            socket.current.emit("move", { destination: "DISPLAY_3", data: dataToBeSend, component: ["COMPONENT1"] });
                            setIsShownOnScreen("DISPLAY_3");
                            isEventTriggered.current = true
                        } else {
                            console.log("left");
                            socket.current.emit("move", { destination: "DISPLAY_1", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_1");
                            isEventTriggered.current = true
                        }
                    } else if (Math.abs(dirY) > movingThreshold) {
                        if (dirY < 0) {
                            console.log("up");
                            socket.current.emit("move", { destination: "DISPLAY_2", data: dataToBeSend, component: ['COMPONENT1'] });
                            setIsShownOnScreen("DISPLAY_2");
                            isEventTriggered.current = true
                        }
                    }
                }

                const sensitivity = 0.3; // Adjust the sensitivity as needed

                const scaledX = mx * sensitivity;
                const scaledY = my * sensitivity;

                api.start({
                    x: x.get() + scaledX,
                    y: y.get() + scaledY,
                });
            },

        },
        { domTarget, eventOptions: { passive: false } }
    );

    const handleOnClick = () => {
        setIsShownOnScreen(undefined);
        isEventTriggered.current = false;
    };

    return (
        <>
            <animated.div
                id="component6"
                className="card"
                style={{
                    x,
                    y,
                }}
            >
                <div id="app-container">
                    <div id="app">
                        <div ref={domTarget} id="menu-toggle-indicator" onClick={handleOnClick}>
                            <CollageCircle />
                        </div>
                    </div>
                </div>
                {isShownOnScreen &&
                    <button onClick={async () => {
                        socket.current.emit("close", { destination: isShownOnScreen, component: 'COMPONENT1' });
                        setIsShownOnScreen(undefined);
                        isEventTriggered.current = false;
                    }}>Close</button>
                }
            </animated.div>
        </>
    );
}
